.section#jumbo {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ff621a+0,313135+91 */
    background: #ff621a; /* Old browsers */
    background: -moz-linear-gradient(top, #e64900 0%, #313135 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #e64900 0%,#313135 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #e64900 0%,#313135 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff621a', endColorstr='#313135',GradientType=0 ); /* IE6-9 */

    color: $vb-light;
    a {
        text-decoration: none;
        color: $vb-light;
        font-weight:$font-semi-bold;
        &:hover,
        &:focus {
            text-decoration: none;
            color: $vb-light;
            background-color: $vb-primary;
        }
    }
}

