button {
    background-color: transparent;
    border: 0;

    @include box-shadow(0, 0, 0, 0);
}

.btn {
    text-transform: uppercase ;
    font-weight: 400;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    padding: 16px;
}

.btn-container {
    display: block;
    width: 100%;
    background-color: $vb-dark;

    a.btn{
        text-decoration: none;
        color: vb-light;
    }
    .col{
        padding: 0;
    }
}

.btn-navbar-brand {
    .pre-hover {
        display: initial;
    }

    .post-hover {
        display: none;
    }

    &:hover,
    &:focus,
    &:active {
        .pre-hover {
            display: none;
        }

        .post-hover {
            display: initial;
        }
    }
}

.vb-btn-full {
    background-color: $vb-grey;
    position: relative;
    overflow: hidden;
    width: 100%;
    border: 2px 1px 2px 1px;
    font-size: 22px;
    height: 63px;
    cursor: pointer;
    color: $vb-light;

    @include border-radius(0);

    &:hover {
        color: $vb-light;
        background-color: $vb-primary;
    }

    &:active,
    &:focus {
        color: $vb-light;
        background-color: $vb-secondary;
    }
}

.vb-btn-disabled {
    @extend .vb-btn-full;

    opacity: 1;
}

.dropdown-menu {
    padding: 0;
    margin: 0;
    left:0;
    background-color: transparent;
    outline: none;
    border: none;
    width:auto;
    @include box-shadow(0, 0, 0, 0);
    li {
        float:left;
        .btn {

             width:256px;
        }
    }
}

.data-nav {
    z-index: 1;
    width: auto;
    float: right;
}

.active {
    color: $vb-light;
    background-color: $vb-primary;
}

.btn:active, .btn.active {
    @include box-shadow(0, 0, 0, 0);
}

.data-menu {
    .active {
        color: $vb-light;
        background-color: $vb-primary;
    }
}

.data-button {
    @include border-radius(0);
    margin: 0;
    background-color: $vb-grey;
    // width: auto;
    color: #fff;
    height: 48px;
    padding: 8px 16px;
    font-weight: 300;
    float: left;
    // width:128px;
    &:hover,
    &:focus {
        color: $vb-light;
        background-color: $vb-primary;
    }
}

.vb-btn {
    font-size: 12px;
    background-color: transparent;
    height: 48px;
    width: 48px;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding:0;
    // padding: 6px 6px 0 6px;
    cursor: pointer;
    color: $vb-primary;
    font-weight: 500;

    @include border-radius(0);

    border: solid 2px transparent;

    &:hover {
        color: $vb-light;
        background-color: $vb-primary;
    }

    &:active,
    &:focus {
        height: 48px;
        width: 48px;
        color: $vb-light;
        background-color: $vb-secondary;
        border: solid 2px $vb-secondary;
    }

    .bar {
        &:before {
            content: "";
            position: absolute;
            top: 28%;
            left: 9px;
            width: 30px;
            height: 22px;
            border-top: 13px double #fff;
            border-bottom: 4px solid #fff;
        }
    }
}

.vb-btn-nav-icon{
    height:auto;
}

.vb-btn-icon {
    cursor: pointer;
    float: left;
    width: 48px;
    padding: 0;
    margin: 0;

    &:hover,
    &:focus {
        opacity: 0.5;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: $vb-grey;
    }

    &:last-child:after {
        padding: 32px;
    }
}

.vb-btn-text {
    font-size: 24px;
    background-color: $vb-grey;
    height: 48px;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 4px 16px 0 16px;
    width: auto;
    cursor: pointer;
    color: $vb-primary;

    @include border-radius(0);

    &:hover,
    &:focus {
        padding: 4px 16px 0 16px;
        height: 48px;
        color: $vb-light;
        background-color: $vb-primary;
    }
}

.vb-btn-text-dark {
    @extend .vb-btn-text;

    background-color: $vb-dark;

    &:hover,
    &:focus {
        height: 48px;
        background-color: $vb-primary;
    }
}

.vb-btn-down {
    font-size: 1.2em;
    cursor: default;
    background-color: $vb-grey;

    @include border-radius(0);

    a {
        &:hover,
        &:focus,
        &:active {
            outline: none;
            border: none;
            text-decoration: none;
            color: $vb-primary;
        }
    }
}

.vb-btn-dark {
    height: 48px;
    width: 48px;
    font-size: 28px;
    font-weight: 500;
    color: #fff;
    border: 0;

    @extend .vb-btn;

    padding: 0;
    background-color: $vb-dark;

    &:hover {
        padding: 0;
        background-color: $vb-primary;
    }

    &:active,
    &:focus {
        height: 48px;
        width: 48px;
        color: $vb-light;
        background-color: $vb-secondary;
        border: solid 2px $vb-secondary;
    }
}

