            padding: 8px;
            font-weight: 300;
            float: left;

            &:hover,
            &:focus {
                color: $vb-light;
                background-color: $vb-primary;
            }

            display: none;
        }

        .data-button {


            display: inherit;
        }

        .active {
            color: $vb-light;
            background-color: $vb-primary;
        }
    }

    .btn-nav-container {
        padding: 16px 0 0 0;
        margin: 0;
        width: 100%;
        display: block;
    }

    .vb-btn-submit {
        background-color: $vb-primary;
        position: relative;
        overflow: hidden;
        width: auto;
        border: solid 2px $vb-primary;
        padding: 0 64px;
        font-size: 22px;
        height: 64px;
        cursor: pointer;
        color: $vb-light;

        @include border-radius(0);

        &:hover {
            color: $vb-primary;
            background-color: transparent;
        }

        &:active,
        &:focus {
            color: $vb-light;
            background-color: $vb-secondary;
            border: solid 2px $vb-secondary;
        }
    }

    .vb-btn-down {
        font-size: 0.6em;
        cursor: default;
        background-color: $vb-grey;

        @include border-radius(0);

        a {
            &:hover,
            &:focus,
            &:active {
                outline: none;
                border: none;
                text-decoration: none;
                color: $vb-primary;
            }
        }
    }
}

