.form-container{
    margin:16px 0
}

form {
    input[type="text"]:disabled {
        cursor: not-allowed;
        opacity: 0.25;
    }

    .input-sm, .input-lg {
        @include border-radius(0);
    }

    .col-md-6, .col-md-4, .col-sm-6, .col-xs-6, .col-md-8, .col-md-10, .col-md-12, .col-md-8 {
        padding-right: 8px;
        margin-left: 0;

        // margin-right:8px;
   
    }

    input, textarea {
        font-size: 24px;
        font-weight: 300px;
        padding: 4px;
        margin: 0;
    }
}

.socialContainer {
    padding: 16px 0 32px 0;
    width: 144px;
    margin: auto;
}

.feedback {
    font-size: 24px;
    display: none;
}

.form-control {
    @include border-radius(0);

    height: 48px;
    font-size: 24px;
}

.alert {
    @include border-radius(0);
}

.alert-error {
    background-color: #eac7c6;
    color: #ba4845;
}

.checkbox input[type=checkbox],
.checkbox-inline input[type=checkbox],
.radio input[type=radio],
.radio-inline input[type=radio] {
    position: relative;
    margin-right: 4px;
}

textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
    color: $vb-grey;
    border: solid 1px $vb-grey;
    outline: 0 none;
    opacity: 0.6;
}

textarea:hover,
input[type="text"]:hover,
input[type="password"]:hover,
input[type="datetime"]:hover,
input[type="datetime-local"]:hover,
input[type="date"]:hover,
input[type="month"]:hover,
input[type="time"]:hover,
input[type="week"]:hover,
input[type="number"]:hover,
input[type="email"]:hover,
input[type="url"]:hover,
input[type="search"]:hover,
input[type="tel"]:hover,
input[type="color"]:hover,
.uneditable-input:hover {
    color: #000;
    border: solid 1px $vb-grey;
    border-right: solid 8px $vb-grey;
    outline: 0 none;
    opacity: 0.75;
}

textarea:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.uneditable-input:focus {
    color: #000;
    border: solid 1px $vb-secondary;
    border-right: solid 8px $vb-secondary;
    outline: 0 none;
    opacity: 1;
}

.form-input {
    width: 100%;
    padding: 6px 8px;
    border: none;
    list-style-type: none;
    list-style-position: outside;
    margin-top: 0;
    background-color: $vb-light;
}

.parsley-required, .parsley-errors-list {
    font-size: 24px;
    color: $vb-primary;
    font-weight: 500;
}

.form-text {
    width: 100%;
    padding: 6px 8px;
    border: none;
    list-style-type: none;
    list-style-position: outside;
    margin-top: 0;
    background-color: $vb-light;

    //background: transparent url(http://s24.postimg.org/62v2ipx81/underline.png) repeat;
    resize: vertical;
    border: solid 1px $vb-grey;
    opacity: 0.75;

    &:focus {
        color: $vb-grey;
        border: solid 1px $vb-secondary;
        border-right: solid 8px $vb-secondary;
        outline: 0 none;
        opacity: 1;
    }
}


