@media (max-width: $break-huge) and (min-width: $break-large) {
    #contact-text {
        margin: auto;
        width: 900px;
    }

    form {
        .col-md-2, .col-md-4, .col-md-6, .col-sm-6, .col-md-8, .col-md-10, .col-md-12, .col-md-8 {
            padding-right: 8px;
            margin-left: 0;
        }

        p {
            font-size: 32px;
            font-weight: 300px;
            padding-bottom: 4px;
        }

        input, textarea {
            font-size: 24px;
            font-weight: 300px;
            padding: 4px;
        }

        input {
            height: 64px;
        }

        textarea {
            height: 128px;
        }
    }
}

@media (max-width: $break-large) and (min-width: $break-big) {
    #contact-text {
