            padding-left: 0;
            margin-left: 0;
        }

        p {
            font-size: 32px;
            font-weight: 300px;
            padding-bottom: 4px;
        }

        input, textarea {
            font-size: 24px;
            font-weight: 300px;
            padding: 4px;
        }

        input {
            height: 64px;
        }

        textarea {
            height: 128px;
        }
    }
}

//1200px< & >767px
@media (max-width: $break-big) and (min-width: $break-medium) {
