        .col-xs-6, .col-xs-8, .col-xs-10 {
            padding-left: 0;
            margin-left: 0;
        }

        p {
            font-size: 28px;
            font-weight: 300px;
            padding-bottom: 4px;
        }

        input, textarea {
            font-size: 18px;
            font-weight: 300px;
            padding: 4px;
        }

        input {
